@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fascinate&display=swap);
:root{
  --axis-y: -1rem;
}

@-webkit-keyframes wave {0%, 100% {-webkit-transform: translatey(0);transform: translatey(0)}50% {-webkit-transform: translatey(-1rem);transform: translatey(-1rem);-webkit-transform: translatey(var(--axis-y));transform: translatey(var(--axis-y))}}

@keyframes wave {0%, 100% {-webkit-transform: translatey(0);transform: translatey(0)}50% {-webkit-transform: translatey(-1rem);transform: translatey(-1rem);-webkit-transform: translatey(var(--axis-y));transform: translatey(var(--axis-y))}}
@-webkit-keyframes swirl-in-fwd{0%{-webkit-transform:rotate(-540deg) scale(0);transform:rotate(-540deg) scale(0);opacity:0}100%{-webkit-transform:rotate(0) scale(1);transform:rotate(0) scale(1);opacity:1}}
@keyframes swirl-in-fwd{0%{-webkit-transform:rotate(-540deg) scale(0);transform:rotate(-540deg) scale(0);opacity:0}100%{-webkit-transform:rotate(0) scale(1);transform:rotate(0) scale(1);opacity:1}}
@-webkit-keyframes slide-in-bck-center{0%{-webkit-transform:translate(0,0,360deg);transform:translate(0,0,360deg);opacity:0}100%{-webkit-transform:translate(0,0,0);transform:translate(0,0,0);opacity:1}}
@keyframes slide-in-bck-center{0%{-webkit-transform:translate(0,0,360deg);transform:translate(0,0,360deg);opacity:0}100%{-webkit-transform:translate(0,0,0);transform:translate(0,0,0);opacity:1}}
@-webkit-keyframes roll-in-left{0%{-webkit-transform:translateX(-800px) rotate(-540deg);transform:translateX(-800px) rotate(-540deg);opacity:0}100%{-webkit-transform:translateX(0) rotate(0deg);transform:translateX(0) rotate(0deg);opacity:1}}
@keyframes roll-in-left{0%{-webkit-transform:translateX(-800px) rotate(-540deg);transform:translateX(-800px) rotate(-540deg);opacity:0}100%{-webkit-transform:translateX(0) rotate(0deg);transform:translateX(0) rotate(0deg);opacity:1}}

.roll-in-left{-webkit-animation:roll-in-left .5s ease-out both;animation:roll-in-left .5s ease-out both}
.wave {-webkit-animation:wave .5s ease-in-out 1;animation:wave .5s ease-in-out 1;}
.swirl-in-fwd{-webkit-animation:swirl-in-fwd .5s ease-out both;animation:swirl-in-fwd .5s ease-out both}
.wave-infinite{-webkit-animation:wave 1.5s ease-in-out infinite;animation:wave 1.5s ease-in-out infinite}
.slide-in-bck-center{-webkit-animation:slide-in-bck-center 7s cubic-bezier(.25,.46,.45,.94) both;animation:slide-in-bck-center 7s cubic-bezier(.25,.46,.45,.94) both}

html,body,#root{
  height:100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgba(211, 255, 129, 0.164);
}

.main{
  min-height:100%;
  position: relative;
  display: grid;
  grid-template-rows: auto 1fr;
}

.header{
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  padding:1rem;
  margin: 2rem .5rem 2rem .5rem;

  position:relative;
  left:50%;
  -webkit-transform: translateX(-50%) rotateZ(-1deg);
          transform: translateX(-50%) rotateZ(-1deg);
  width:90%;

  display:-webkit-flex !important;

  display:flex !important;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex:0 1;
          flex:0 1;
  box-shadow: .5rem .5rem .5rem  rgb(0, 52, 131);
  background-color: rgb(0, 102, 255);
  color: white;
  
  font-family: 'Source Sans Pro', sans-serif;
  font-size:5vw;
}


.title{
  font-family: Fascinate, sans-serif;
  text-align: center;
  
}

.sub-title{
  text-transform: uppercase;
  font-family: "Sans Source Pro", sans-serif;
  font-size:1.85vw;
  text-align: center;
}

.title-char{
  
}

.title2{
  position: absolute;
  left:50%;
  -webkit-transform: translate(-49%,3%);
          transform: translate(-49%,3%);
  color:rgb(0, 7, 100);
  z-index:-1;
}

.sub-title-char{
  box-shadow: .2rem .2rem .05rem  rgb(33, 0, 59);
  margin:0 .1rem;
  padding: 0 .25rem;
  
  background-color: rgb(140, 0, 255);
  color: white;
}

.under-contruction{
  padding:1rem;
  width:70%;
  -webkit-transform: rotateZ(1deg);
          transform: rotateZ(1deg);
  background-color:rgb(255, 255, 255);
  box-shadow: .2rem .2rem .05rem  rgb(221, 125, 0);
  color:  rgb(255, 145, 0);
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3vw;
}
